import Cookie from "js-cookie";
import day from "dayjs";

const utils = {
    /**
     * 
     */
    isEven(num) {
        return (num % 2) === 0
    },
    /**
     * 100
     */
    paddingLeft(num) {
        return num < 10 ? "0" + num : num;
    },
    /**
     * 
     */
    formatTime(mss) {
        var h = parseInt((mss / (1000 * 60 * 60)) % 60);
        var m = parseInt((mss / (1000 * 60)) % 60);
        var s = parseInt((mss / 1000) % 60);
        return `${h > 0 ? this.paddingLeft(h) + ':' : ''}${m > 0 ? this.paddingLeft(m) + ':' : "00"}${s > 0 ? this.paddingLeft(s) + '' : "00"}`;
    },
    // 
    unique(arr) {
        const res = new Map();
        return arr.filter((arr) => !res.has(arr.id) && res.set(arr.id, 1))
    },
    // type
    getTextByType(type) {
        switch (type) {
            case "1":
              return "";
            case "100":
                return "";
            case "10":
                return "";
            case "1014":
                return "";
            case "1006":
                return "";
            case "1000":
                return "";
            case "1009":
                return "";
            case "1002":
                return "";
            default:
              break;
          }
    },
    // cookie
    setCookie(key, value) {
        Cookie.set(key, value);
    },
    // cookie
    getCookie(key) {
        return Cookie.get(key);
    },
    formatDate(time) {
        return day(time).format('YYYY-MM-DD')
    },
    
    /**
     * 
     */
    goAnchor(selector) {
        let dom = document.querySelector(selector);
        let top = dom.offsetTop;
        window.scrollTo(0, top);
    }
};

export default utils;