import React from 'react';
import style from "./today.module.less";
import { Link } from 'react-router-dom';
import dayjs from 'dayjs';

const dayMap = {
    "1": "",
    "2": "",
    "3": "",
    "4": "",
    "5": "",
    "6": "",
    "7": "",
}

export default function Today() {
    return (
        <div className={style['today-wrap']}>
            <Link className="item" to="/find/recommend/taste">
                <span className="head">{dayMap[dayjs().day()]}</span>
                <span className="bd">{dayjs().date()}</span>
                <span className="mask"></span>
            </Link>
            <Link className="desc" to="/find/recommend/taste"></Link>
            <p className="idv"><br/>6:00</p>
        </div>
    )
}
